// AIAPI
import request from './request.js';

export default {
  /**
   * AI
   * @param {String} question - 
   * @returns {Promise} - AI
   */
  askQuestion(question) {
    return request.post('/api/ai-chat/question', question, {
      header: {
        'Content-Type': 'text/plain'
      }
    });
  }
};