// API
import request from './request.js';

export default {
  /**
   * 
   * @param {Object} data -  {username, password, role}
   * @returns {Promise} - 
   */
  login(data) {
    return request.post('/api/users/login', data);
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise} - 
   */
  register(data) {
    return request.post('/api/users/register', data);
  },

  /**
   * 
   * @param {Number} id - ID
   * @returns {Promise} - 
   */
  getUserInfo(id) {
    return request.get(`/api/users/${id}`);
  },
  
  /**
   * 
   * @param {Object} data - 
   * @returns {Promise} - 
   */
  updatePassword(data) {
    return request.post('/api/users/password/update', data);
  },
  
  /**
   * 
   * @param {String} username - 
   * @returns {Promise} - 
   */
  searchUser(username) {
    return request.get(`/api/users/search?username=${username}`);
  },
  
  /**
   * 
   * @param {Number} id - ID
   * @param {Object} data - 
   * @returns {Promise} - 
   */
  updateUser(id, data) {
    return request.put(`/api/users/${id}`, data);
  },
  
  /**
   * 
   * @param {Number} id - ID
   * @returns {Promise} - 
   */
  resetPassword(id) {
    return request.post(`/api/users/${id}/password/reset`);
  },
  
  /**
   * 
   * @param {String} username - 
   * @param {Object} file - 
   * @returns {Promise} - 
   */
  uploadAvatar(username, file) {
    const formData = new FormData();
    formData.append('file', file);
    return request.post(`/api/users/${username}/avatar`, formData);
  },

  /**
   * 
   * @param {Object} data - 
   * @returns {Promise} - 
   */
  changePassword(data) {
    const userInfo = uni.getStorageSync('userInfo');
    if (!userInfo || !userInfo.username) {
      return Promise.reject(new Error(''));
    }
    
    return request.post('/api/users/password/update', {
      username: userInfo.username,
      oldPassword: data.oldPassword,
      newPassword: data.newPassword
    });
  },

  /**
   * 
   * @returns {Promise} - 
   */
  getAllUsers() {
    return request.get('/api/users');
  },

  /**
   * 
   * @param {Number} id - ID
   * @returns {Promise} - 
   */
  deleteUser(id) {
    return request.delete(`/api/users/${id}`);
  }
}; 