const baseURL = 'http://localhost:8080';

const request = {
  /**
   * GET
   * @param {String} url - 
   * @param {Object} params - 
   * @param {Object} header - 
   * @returns {Promise} - Promise
   */
  get(url, params = {}, header = {}) {
    return new Promise((resolve, reject) => {
      // token
      const token = uni.getStorageSync('token');
      
      // 
      const headers = {
        'Content-Type': 'application/json',
        ...header
      };
      
      // token
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      uni.request({
        url: baseURL + url,
        method: 'GET',
        data: params,
        header: headers,
        success: (res) => {
          if (res.statusCode === 200) {
            resolve(res.data);
          } else if (res.statusCode === 401) {
            // 
            uni.removeStorageSync('token');
            uni.removeStorageSync('userInfo');
            // 
            uni.showToast({
              title: '',
              icon: 'none'
            });
            setTimeout(() => {
              uni.navigateTo({
                url: '/pages/my/login'
              });
            }, 1500);
            reject(res);
          } else {
            // 
            reject(res);
          }
        },
        fail: (err) => {
          reject(err);
        }
      });
    });
  },

  /**
   * POST
   * @param {String} url - 
   * @param {Object|String} data - 
   * @param {Object} header - 
   * @returns {Promise} - Promise
   */
  post(url, data = {}, header = {}) {
    return new Promise((resolve, reject) => {
      // token
      const token = uni.getStorageSync('token');
      
      // 
      const headers = {
        'Content-Type': 'application/json',
        ...header
      };
      
      // token
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      console.log(`POST ${url}`, data, headers);
      
      uni.request({
        url: baseURL + url,
        method: 'POST',
        data: data,
        header: headers,
        success: (res) => {
          console.log(`:`, res);
          if (res.statusCode === 200) {
            resolve(res.data);
          } else if (res.statusCode === 401) {
            // 
            uni.removeStorageSync('token');
            uni.removeStorageSync('userInfo');
            // 
            uni.showToast({
              title: '',
              icon: 'none'
            });
            setTimeout(() => {
              uni.navigateTo({
                url: '/pages/my/login'
              });
            }, 1500);
            reject(res);
          } else {
            console.error(':', res.statusCode, res.data);
            reject(res.data || res);
          }
        },
        fail: (err) => {
          console.error(':', err);
          reject(err);
        }
      });
    });
  },

  /**
   * PUT
   * @param {String} url - 
   * @param {Object|String} data - 
   * @param {Object} header - 
   * @returns {Promise} - Promise
   */
  put(url, data = {}, header = {}) {
    return new Promise((resolve, reject) => {
      // token
      const token = uni.getStorageSync('token');
      
      // 
      const headers = {
        'Content-Type': 'application/json',
        ...header
      };
      
      // token
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      uni.request({
        url: baseURL + url,
        method: 'PUT',
        data: data,
        header: headers,
        success: (res) => {
          if (res.statusCode === 200) {
            resolve(res.data);
          } else if (res.statusCode === 401) {
            // 
            uni.removeStorageSync('token');
            uni.removeStorageSync('userInfo');
            // 
            uni.showToast({
              title: '',
              icon: 'none'
            });
            setTimeout(() => {
              uni.navigateTo({
                url: '/pages/my/login'
              });
            }, 1500);
            reject(res);
          } else {
            reject(res);
          }
        },
        fail: (err) => {
          reject(err);
        }
      });
    });
  },

  /**
   * DELETE
   * @param {String} url - 
   * @param {Object} params - 
   * @param {Object} header - 
   * @returns {Promise} - Promise
   */
  delete(url, params = {}, header = {}) {
    return new Promise((resolve, reject) => {
      // token
      const token = uni.getStorageSync('token');
      
      // 
      const headers = {
        'Content-Type': 'application/json',
        ...header
      };
      
      // token
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      uni.request({
        url: baseURL + url,
        method: 'DELETE',
        data: params,
        header: headers,
        success: (res) => {
          if (res.statusCode === 200) {
            resolve(res.data);
          } else if (res.statusCode === 401) {
            // 
            uni.removeStorageSync('token');
            uni.removeStorageSync('userInfo');
            // 
            uni.showToast({
              title: '',
              icon: 'none'
            });
            setTimeout(() => {
              uni.navigateTo({
                url: '/pages/my/login'
              });
            }, 1500);
            reject(res);
          } else {
            reject(res);
          }
        },
        fail: (err) => {
          reject(err);
        }
      });
    });
  }
};

export default request; 